import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';


import { BluetoothSerial } from '@ionic-native/bluetooth-serial';

import { BlePrinterService } from '../../services/BlePrinterService';

import * as str2ab from 'string-to-arraybuffer';

import { TextEncoder, TextDecoder } from 'text-encoding-shim';

import * as cptable from 'codepage/cputils.js';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

	MACAddress = '88:6B:0F:97:C4:D9';

  constructor(	public navCtrl: NavController,
  				private bluetoothSerial: BluetoothSerial,
          private blePrinterService: BlePrinterService) {

  }

 

  list(){
  	//88:6B:0F:97:C4:D9
  	console.log('==> list')
  	this.bluetoothSerial.list().then(res => {
  		console.log('list res')
  		console.log(res)
  	})
  }



  printToBLEPrinter(){ 
    console.log('==> printToBLEPrinter')
    // const printData:String = `!0 200 200 50 1
    //                     TEXT 4 0 10 10 Hello World
    //                     FORM
    //                     PRINT`
    //   ;

 
 


    const CHAR_ESC ='\u001b';
    const LINE_FEED = '\n';
    const CARRIAGE_RETURN = '\r';

    let data = `${CHAR_ESC} ! 0 200 200 500 1 ${CARRIAGE_RETURN} ${LINE_FEED} 
                ENCODING BIG5   ${CARRIAGE_RETURN} ${LINE_FEED}
                SETMAG 2 2
                TEXT 0 0 10 10 ==> 22繁體中文讚讚讚繁體中文讚讚讚繁體中文讚讚讚 ${CARRIAGE_RETURN} ${LINE_FEED}   
                
                SETMAG 4 4
                TEXT 0 0 10 50 ==> 44繁體中文讚讚讚繁體中文讚讚讚繁體中文讚讚讚 ${CARRIAGE_RETURN} ${LINE_FEED}   
                
                SETMAG 6 6
                TEXT 0 0 10 100 ==> 66繁體中文讚讚讚繁體中文讚讚讚繁體中文讚讚讚 ${CARRIAGE_RETURN} ${LINE_FEED}   
                

                SETMAG 8 8
                TEXT 0 0 10 150 ==> 88繁體中文讚讚讚繁體中文讚讚讚繁體中文讚讚讚 ${CARRIAGE_RETURN} ${LINE_FEED}   
                
                
                SETMAG 10 10
                TEXT 0 0 10 200 ==> 1010繁體中文讚讚讚繁體中文讚讚讚繁體中文讚讚讚 ${CARRIAGE_RETURN} ${LINE_FEED}   

                FORM  ${CARRIAGE_RETURN} ${LINE_FEED}
                PRINT ${CARRIAGE_RETURN} ${LINE_FEED}
 
    `


    console.log('data')
    console.log(data)


    let uint8array = new TextEncoder('utf-8').encode(data);
    let string = new TextDecoder('utf-8').decode(uint8array);

 

    console.log('uint8array')
    console.log(uint8array);

    console.log('string')
    console.log(string);



    console.log(cptable.utils.encode(950,  '中'));
 

       
    this.blePrinterService.testPrint(this.MACAddress, cptable.utils.encode(950,  data));
  }







  arrayPrint(){ 
    this.blePrinterService.testPrint(this.MACAddress, [228, 184, 173]); 
  }



 























 


}



